.review-form {
  width: 280px;
  margin-left: 20px;
  margin-right: 20px;

  .review-form__field-title {
    max-width: 238px;
    margin: 0;
    margin-bottom: 20px;
    text-align: center;
  }

  .review-form__name-field {
    margin-top: 24px;
    margin-bottom: 39px;
    padding: 0;
    border: 0;
    margin-left: 0;
    margin-right: 0;
  }

  .review-form__contact-field {
    margin-top: 24px;
    margin-bottom: 34px;
    padding: 0;
    border: 0;
    margin-left: 0;
    margin-right: 0;
  }

  .review-form__impression {
    margin-top: 24px;
    margin-bottom: 24px;
    padding: 0;
    border: 0;
    margin-left: 0;
    margin-right: 0;
  }

  .review-form__sight {
    margin-top: 24px;
    margin-bottom: 24px;
    padding: 0;
    border: 0;
    margin-left: 0;
    margin-right: 0;
  }

  .review-form__list {
    margin: 0;
    padding: 0;
    list-style-type: none;
  }

  .review-form__item {
    margin-bottom: 16px;
  }

  .review-form__review {
    margin-top: 24px;
    margin-bottom: 56px;
  }

  .review-form__button {
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 21px;
    line-height: @line-height-subtitle;
    color: @color-blue-bcgr;
    text-transform: @text-transform-all-page;
    background-color: @background-color;
    cursor: pointer;
    border: none;
    padding: 19px 34px 13px 32px;
    margin: 0;
    margin-bottom: 3px;
    width: 280px;
  }

  .review-form__button:hover {
    background-color: #669ec0;
  }

  .review-form__button:active {
    background-color: #5496bd;
    color: rgba(255, 255, 255, 0.3);
  }

  .review-form__remark {
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 21px;
    text-transform: uppercase;
    color: rgba(51, 51, 51, 0.6);
    text-align: center;
    padding-left: 73px;
    margin: 0;
  }
}
